<template>
  <div class="body-bg">
    <headerBar :header-data="headerData" />
    <div class="container-ca">
      <div class="login-cainfo">
        <div class="ca-icon"><van-image fit="cover" width="100%" height="100%" :src="imageList.img1" /></div>
        <div class="ca-text">移动证书变更</div>
      </div>
      <div class="login-form">
        <van-cell-group>

          <div class="input-item">
            <van-field
              ref="name"
              v-model="userAccount"
              v-validate="'required'"
              clearable
              is-highlight
              is-title-latent
              placeholder="请输入当前账号密码"
              name="userAccount"
              :error-message="errors.first('userAccount')"
            />
          </div>

          <div class="input-item password">
            <van-field
              ref="id"
              v-model="PIN1"
              v-validate="'required|pinLength'"
              clearable
              is-highlight
              is-title-latent
              maxlength="6"
              type="number"
              placeholder="请输入移动证书PIN码"
              name="PIN"
              :error-message="errors.first('PIN')"
            />
            <!-- 显示与隐藏密码 -->
            <div v-if="eyesChange1 ==true" class="input-operator showpassword" @click="showEye1()">
              <i class="iconfont iconkejian" />
            </div>
            <div v-else class="input-operator showpassword" @click="showEye1()">
              <i class="iconfont iconbukejian" />
            </div>
          </div>

          <div class="input-item password">
            <van-field
              ref="id"
              v-model="PIN2"
              v-validate="'required|pinLength'"
              clearable
              is-highlight
              is-title-latent
              maxlength="6"
              type="number"
              placeholder="请再次输入移动证书Pin码"
              name="PIN"
              :error-message="errors.first('PIN')"
            />
            <!-- 显示与隐藏密码 -->
            <div v-if="eyesChange2 ==true" class="input-operator showpassword" @click="showEye2()">
              <i class="iconfont iconkejian" />
            </div>
            <div v-else class="input-operator showpassword" @click="showEye2()">
              <i class="iconfont iconbukejian" />
            </div>
          </div>

        </van-cell-group>
        <van-notice-bar
          style="margin-top:15px;"
          left-icon="info-o"
          text="此密码将作为您以后的移动证书登录密码,请妥善保管！"
        />
        <div class="login-button">
          <van-button round type="info">安装证书</van-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter, NativeCaJS, NativeJS } from '@/common/js/mixin.js'
export default {
  name: 'CaBg',
  components: {
    headerBar
  },
  mixins: [pageRouter, NativeCaJS, NativeJS],
  data() {
    return {
      userAccount: '',
      PIN2: '',
      PIN1: '',
      eyesChange1: false,
      eyesChange2: false,
      imageList: {
        img1: require('@/assets/images/login/icon-ca.png'),
        tipsimg1: require('@/assets/images/icon-success.png'), /* 成功 */
        tipsimg2: require('@/assets/images/icon-info.png'), /* 警告 */
        tipsimg3: require('@/assets/images/icon-error.png') /* 失败 */
      },
      headerData: {
        title: '证书变更',
        leftUrl: 'back',
        leftText: '首页'
      }
    }
  },

  methods: {
    showEye1() {
      //  eyesChange1: false,
      //     eyesChange2: false,
      this.eyesChange1 = !this.eyesChange1
    },
    showEye2() {
      this.eyesChange2 = !this.eyesChange2
    },
    goCaxx() {
      this.$router.push({
        name: 'Caxx'
      })
    }
  }

}

</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
